import { useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import IqacReportAuditTab from "./IqacReportAuditTab";
import AuditReportAAA from "./AuditReportAAA/AuditReportAAA";
import AuditReportISO from "./AuditReportISO/AuditReportISO";
import AuditReportGreen from "./AuditReportGreen/AuditReportGreen";
import AuditReportFireSafety from "./AuditReportFireSafety/AuditReportFireSafety";
import AuditReportCertificate from "./AuditReportCertificate/AuditReportCertificate";
import AuditReportGender from "./AuditReportGender/AuditReportGender";
import AuditReportLab from "./AuditReportLab/AuditReportLab";
import AuditReportEnvrionment from "./AuditReportEnvrionment/AuditReportEnvrionment";

const IqacReportAudit = ({ qcid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();

  return (
    <>
      {searchParams.get("n") === "aaa" ? (
        <AuditReportAAA qcid={qcid} />
      ) : searchParams.get("n") === "iso" ? (
        <AuditReportISO qcid={qcid} />
      ) : searchParams.get("n") === "green" ? (
        <AuditReportGreen qcid={qcid} />
      ) : searchParams.get("n") === "environment" ? (
        <AuditReportEnvrionment qcid={qcid} />
      ) : searchParams.get("n") === "lab" ? (
        <AuditReportLab qcid={qcid} />
      ) : searchParams.get("n") === "gender" ? (
        <AuditReportGender qcid={qcid} />
      ) : searchParams.get("n") === "certificate" ? (
        <AuditReportCertificate qcid={qcid} />
      ) : searchParams.get("n") === "fire-safety" ? (
        <AuditReportFireSafety qcid={qcid} />
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>{t("reports_of_audit")}</h6>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />

          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0.3rem",
            }}
          >
            <IqacReportAuditTab />
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}
    </>
  );
};

export default IqacReportAudit;
