import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import SyllabusFeedbackCard from "./SyllabusFeedbackCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddSyllabusFeedbackStudent from "./AddSyllabusFeedbackStudent";
const SyllabusFeedbackStudent = ({ qcid }) => {
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <div className={style.fm_header_container}>
        <h6></h6>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>

      {iqacDashboardMaster?.syllabus_feedback_object?.map((feedback) =>
        feedback?.flow === "TEACHERS" ? null : (
          <SyllabusFeedbackCard feedback={feedback} key={feedback?._id} />
        )
      )}
      {iqacDashboardMasterLoading ? (
        <Loading />
      ) : (
        iqacDashboardMaster?.syllabus_feedback_object?.length === 0 && (
          <EmptyMessage title="No Student Syllabus feedback." />
        )
      )}

      {add && (
        <AddSyllabusFeedbackStudent
          onClose={onToggle}
          qcid={qcid}
          onRefetch={iqacDashboardMasterRefetch}
        />
      )}
    </>
  );
};

export default SyllabusFeedbackStudent;
