import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import AddIqacInstitutional from "./AddIqacInstitutional";
import style from "../../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useIqacExtraAbout } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import IqacInstitutionalCard from "./IqacInstitutionalCard";
import Loading from "../../../../Loader/Loading";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { useInView } from "react-intersection-observer";
const IqacInstitutional = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { iqacExtraAbout, iqacExtraAboutLoading, iqacExtraAboutRefetch } =
    useIqacExtraAbout({
      data: {
        qcid: qcid,
        page: page,
        limit: 10,
        search: search,
        flow: "IQAC",
        type: "IQAC_POLICY",
      },
      skip: !qcid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (iqacExtraAbout?.list?.length) {
      let obj = nextPage(iqacExtraAbout?.list?.length);
      setState(() => obj);
    }
  }, [iqacExtraAbout?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    iqacExtraAboutRefetch();
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("policy")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </div>
      </UserMemberContentWrapper>

      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
        }}
      >
        {iqacExtraAbout?.list?.map((sub, index) =>
          iqacExtraAbout?.list?.length === index + 1 ? (
            <div key={sub?._id} ref={ref}>
              <IqacInstitutionalCard sub={sub} />
            </div>
          ) : (
            <IqacInstitutionalCard sub={sub} key={sub?._id} />
          )
        )}

        {iqacExtraAboutLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          iqacExtraAbout?.list?.length === 0 && (
            <EmptyMessage
              title="No data found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          iqacExtraAbout?.list?.length === 0 && (
            <EmptyMessage
              title="No data."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>

      {add && (
        <AddIqacInstitutional
          onClose={onToggle}
          qcid={qcid}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default IqacInstitutional;
