import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import AddAuditReportAAA from "./AddAuditReportAAA";
import style from "../../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useIqacReoprtAudit } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AuditReportAAACard from "./AuditReportAAACard";
import Loading from "../../../../Loader/Loading";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const AuditReportAAA = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { iqacReoprtAudit, iqacReoprtAuditLoading, iqacReoprtAuditRefetch } =
    useIqacReoprtAudit({
      data: {
        qcid: qcid,
        page: page,
        limit: 10,
        search: search,
        tab_type: "AAA",
      },
      skip: !qcid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (iqacReoprtAudit?.list?.length) {
      let obj = nextPage(iqacReoprtAudit?.list?.length);
      setState(() => obj);
    }
  }, [iqacReoprtAudit?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    iqacReoprtAuditRefetch();
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("aaa")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>

        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.3rem",
          }}
        >
          {iqacReoprtAudit?.list?.map((sub, index) =>
            iqacReoprtAudit?.list?.length === index + 1 ? (
              <div key={sub?._id} ref={ref}>
                <AuditReportAAACard sub={sub} />
              </div>
            ) : (
              <AuditReportAAACard sub={sub} key={sub?._id} />
            )
          )}

          {iqacReoprtAuditLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            iqacReoprtAudit?.list?.length === 0 && (
              <EmptyMessage
                title="No data found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            iqacReoprtAudit?.list?.length === 0 && (
              <EmptyMessage
                title="No data."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>

        {add && (
          <AddAuditReportAAA
            onClose={onToggle}
            qcid={qcid}
            onRefetch={onRefetch}
          />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default AuditReportAAA;
