import { useTranslation } from "react-i18next";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useCallback, useEffect, useState } from "react";
import {
  useIqacDashboardMaster,
  useIqacMemberList,
} from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import style from "../../FinanceManager/FinanceManager.module.css";
import IqacMembersCard from "./IqacMembersCard";
import AddIqacMembers from "./AddIqacMembers";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
const IqacMembers = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { iqacDashboardMaster } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const { iqacMemberList, iqacMemberListLoading, iqacMemberListRefetch } =
    useIqacMemberList({
      data: {
        qcid: qcid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !qcid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (iqacMemberList?.list?.length) {
      let obj = nextPage(iqacMemberList?.list?.length);
      setState(() => obj);
    }
  }, [iqacMemberList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    iqacMemberListRefetch();
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("compositions")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {iqacMemberList?.list?.map((member, index) =>
            iqacMemberList?.list?.length === index + 1 ? (
              <div key={member?._id} ref={ref}>
                <IqacMembersCard member={member} />
              </div>
            ) : (
              <IqacMembersCard member={member} key={member?._id} />
            )
          )}

          {iqacMemberListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            iqacMemberList?.list?.length === 0 && (
              <EmptyMessage
                title="No member found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            iqacMemberList?.list?.length === 0 && (
              <EmptyMessage
                title="No member."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddIqacMembers
          onClose={onToggle}
          qcid={qcid}
          onRefetch={onRefetch}
          instituteId={iqacDashboardMaster?.institute}
        />
      )}
    </>
  );
};

export default IqacMembers;
