import { useLocation, useSearchParams } from "react-router-dom";
import NaacUndertaking from "./NaacUndertaking/NaacUndertaking";
import NaacSSR3 from "./NaacSSR3/NaacSSR3";
import NaacSSR4 from "./NaacSSR4/NaacSSR4";
import NaacDVV from "./NaacDVV/NaacDVV";
import NaacIIQA from "./NaacIIQA/NaacIIQA";
import NaacSurvey from "./NaacSurvey/NaacSurvey";
import NaacCertificate from "./NaacCertificate/NaacCertificate";
import NaacIIPText from "./NaacIIPText/NaacIIPText";

const CommitteeNaacManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      {searchParams.get("n") === "ssr-3" ? (
        <NaacSSR3 qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "ssr-4" ? (
        <NaacSSR4 qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "dvv" ? (
        <NaacDVV qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "iiaq" ? (
        <NaacIIQA qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "certificate" ? (
        <NaacCertificate qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "survey" ? (
        <NaacSurvey qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "undertaking" ? (
        <NaacUndertaking qcid={getQuery.state?.qcid} />
      ) : searchParams.get("n") === "iip" ? (
        <NaacIIPText qcid={getQuery.state?.qcid} />
      ) : null}
    </>
  );
};

export default CommitteeNaacManager;
