import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useIqacAddMember } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import InstituteStaff from "../../InstituteStaff/InstituteStaff";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../FinanceManager/Income/Income.module.css";
import styleEdit from "../../../../Profile/Institute/Pages/Site/SiteEdit.module.css";
import InstituteStudent from "../../InstituteStudent/InstituteStudent";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
const AddIqacMembers = ({ onClose, qcid, onRefetch, instituteId }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    add_as: "STAFF",
    designation: "",
    selected_member: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [assignHead, setAssignHead] = useState(false);
  const [assignHeadStaff, setAssignHeadStaff] = useState("");

  const [iqacAddMember] = useIqacAddMember();

  const onSelectLeading = (assing) => {
    setAssignHeadStaff(assing);
    setState((prev) => ({
      ...prev,
      selected_member: assing?._id,
    }));
    setAssignHead(false);
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      add_as: value,
    }));
  };
  const onCreate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && qcid) {
      setDisabled((pre) => !pre);
      iqacAddMember({
        qcid: qcid,
        bodyParameter: {
          staff_arr:
            state.add_as === "STAFF"
              ? [
                  {
                    staff: state.selected_member,
                    designation: state.designation,
                  },
                ]
              : [],
          student_arr:
            state.add_as === "STUDENT"
              ? [
                  {
                    student: state.selected_member,
                    designation: state.designation,
                  },
                ]
              : [],
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      {assignHead ? (
        <>
          {state.add_as === "STAFF" ? (
            <InstituteStaff
              onSelectStaff={onSelectLeading}
              title={t("select_staff_member")}
              onClose={() => setAssignHead((pre) => !pre)}
              selectedId={state.selected_member}
              instituteId={instituteId}
            />
          ) : (
            <InstituteStudent
              onSelectStudent={onSelectLeading}
              title={t("select_student_member")}
              onClose={() => setAssignHead((pre) => !pre)}
              selectedId={state.selected_member}
              instituteId={instituteId}
            />
          )}
        </>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_members")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <TileWrapper label="member_type">
                {addType?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTile}
                    activeTile={state.add_as}
                  />
                ))}
              </TileWrapper>

              <FormInput
                label={t("designation")}
                placeholder={t("designation_placeholder")}
                name={"designation"}
                value={state.designation}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["designation"]}
              />

              {state.add_as === "STAFF" ? (
                <div
                  className={styleEdit.leading_person_container}
                  onClick={() => setAssignHead(true)}
                  style={{
                    marginTop: "1rem",
                    width: "100%",
                  }}
                >
                  <img
                    src={
                      assignHeadStaff?.staffProfilePhoto
                        ? `${imageShowUrl}/${assignHeadStaff.staffProfilePhoto}`
                        : `${assestsFeedUrl}/feed-user-avatar.svg`
                    }
                    alt="avatar"
                  />
                  <div className={styleEdit.leading_person_text}>
                    <h6>
                      {assignHeadStaff
                        ? `${assignHeadStaff?.staffFirstName ?? ""} ${
                            assignHeadStaff?.staffMiddleName ?? ""
                          } ${assignHeadStaff?.staffLastName ?? ""}`
                        : t("select_staff")}
                    </h6>
                    <p>
                      {t("search_joined_list")}{" "}
                      {errorField["selected_member"] && (
                        <span style={{ color: "red", marginLeft: "15px" }}>
                          * {t("form_require_label")}
                        </span>
                      )}
                    </p>
                  </div>
                </div>
              ) : (
                <div
                  className={styleEdit.leading_person_container}
                  onClick={() => setAssignHead(true)}
                  style={{
                    marginTop: "1rem",
                    width: "100%",
                  }}
                >
                  <img
                    src={
                      assignHeadStaff?.studentProfilePhoto
                        ? `${imageShowUrl}/${assignHeadStaff.studentProfilePhoto}`
                        : `${assestsFeedUrl}/feed-user-avatar.svg`
                    }
                    alt="avatar"
                  />
                  <div className={styleEdit.leading_person_text}>
                    <h6>
                      {assignHeadStaff
                        ? `${assignHeadStaff?.studentFirstName ?? ""} ${
                            assignHeadStaff?.studentMiddleName ?? ""
                          } ${assignHeadStaff?.studentLastName ?? ""}`
                        : t("select_student")}
                    </h6>
                    <p>
                      {t("search_joined_list")}{" "}
                      {errorField["selected_member"] && (
                        <span style={{ color: "red", marginLeft: "15px" }}>
                          * {t("form_require_label")}
                        </span>
                      )}
                    </p>
                  </div>
                </div>
              )}

              <CreateButton
                label={t("add")}
                customStyle={{
                  padding: "0.4rem 1rem",
                }}
                onAction={onCreate}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddIqacMembers;

const addType = [
  {
    key: "STAFF",
    name: "staff",
  },
  {
    key: "STUDENT",
    name: "student",
  },
];
