import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import IqacCard from "./IqacCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddIqacAqar from "./AddIqacAqar";
const IqacAqar = ({ qcid }) => {
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <div className={style.fm_header_container}>
        <h6></h6>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>
      {iqacDashboardMaster?.iqac_aqar?.map((aqarData) => (
        <IqacCard aqarData={aqarData} key={aqarData?._id} />
      ))}
      {iqacDashboardMasterLoading ? (
        <Loading />
      ) : (
        iqacDashboardMaster?.iqac_aqar?.length === 0 && (
          <EmptyMessage title="No AQAR." />
        )
      )}
      {add && (
        <AddIqacAqar
          onClose={onToggle}
          qcid={qcid}
          onRefetch={iqacDashboardMasterRefetch}
        />
      )}
    </>
  );
};

export default IqacAqar;
