import { useState } from "react";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormFile from "../../../../Ui/Input/FormFile";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import { useNaacAddDocument } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import FormInput from "../../../../Ui/Input/FormInput";
const AddNaacCertificate = ({ onClose, onRefetch, qcid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    name: "",
    attach: [],
  });
  const [naacAddDocument] = useNaacAddDocument();
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const [fileUpload] = useFileUpload();

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attach: [...prev.attach, res.data?.imageKey],
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onExistingFileUpload = (e, ind) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          let ip = [...state.attach];
          ip[ind] = res.data?.imageKey;
          setState((prev) => ({
            ...prev,
            attach: ip,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && qcid) {
      setDisabled((pre) => !pre);
      naacAddDocument({
        qcid: qcid,
        bodyParameter: {
          ...state,
          flow: "CERTIFICATE",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_certificate")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("name")}
              placeholder={t("name_certificate_placeholder")}
              name={"name"}
              value={state.name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["name"]}
            />
            {state.attach?.map((st, index) => (
              <FormFile
                key={st}
                label={t("add_attachment")}
                name={"add_attachment"}
                value={
                  st
                    ? {
                        key: st,
                        name: "View",
                      }
                    : ""
                }
                onChange={(e) => onExistingFileUpload(e, index)}
              />
            ))}
            <FormFile
              label={t("add_attachment")}
              name={`add_attachment_other${state.attach?.length}`}
              value=""
              onChange={onFileUpload}
              errorShow={errorField["attach"]}
            />
            <CreateButton label="add" onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddNaacCertificate;
