import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import { Suspense, lazy } from "react";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import CommitteeManagerProfile from "./CommitteeManagerProfile";
import AcademicCalendar from "./AcademicCalendar/AcademicCalendar";
import SSRReports from "./SSRReports/SSRReports";
import SSRDocument from "./SSRDocument/SSRDocument";
import StudentSatisfactorySurvey from "./StudentSatisfactorySurvey/StudentSatisfactorySurvey";
import AnnualReports from "./AnnualReports/AnnualReports";
import Iqac from "./Iqac/Iqac";
import SyllabusFeedback from "./SyllabusFeedback/SyllabusFeedback";
import IqacMembers from "./IqacMembers/IqacMembers";
import IqacAbout from "./IqacAbout/IqacAbout";
import IqacReportAudit from "./IqacReportAudit/IqacReportAudit";
import IqacBestPractice from "./IqacBestPractice/IqacBestPractice";
import IqacInstitutionalDistinctiveness from "./IqacInstitutionalDistinctiveness/IqacInstitutionalDistinctiveness";
import CommitteeNaacManager from "./CommitteeNaacManager/CommitteeNaacManager";
const CommitteeManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();

  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["meeting", "naac"]?.includes(searchParams.get("type")) ? (
            <UserMemberTabsWrapper>
              <CommitteeManagerProfile qcid={getQuery.state?.qcid} />
            </UserMemberTabsWrapper>
          ) : searchParams.get("type") === "calendar" ? (
            <AcademicCalendar qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "ssr-reports" ? (
            <SSRReports qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "ssr-documents" ? (
            <SSRDocument qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "satisfactory-survey" ? (
            <StudentSatisfactorySurvey qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "annual-reports" ? (
            <AnnualReports qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "iqac" ? (
            <Iqac qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "syllabus" ? (
            <SyllabusFeedback qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "member" ? (
            <IqacMembers qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "about" ? (
            <IqacAbout qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "audit" ? (
            <IqacReportAudit qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "practice" ? (
            <IqacBestPractice qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "institutional" ? (
            <IqacInstitutionalDistinctiveness qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "naac-detail" ? (
            <CommitteeNaacManager qcid={getQuery.state?.qcid} />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default CommitteeManager;
