import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../FinanceManager/FinanceManager.module.css";
import NaacSSR4Card from "./NaacSSR4Card";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import AddNaacSSR4 from "./AddNaacSSR4";
import { useNaacDashboardMaster } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
const NaacSSR4 = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    naacDashboardMaster,
    naacDashboardMasterLoading,
    naacDashboardMasterRefetch,
  } = useNaacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("ssr_4")}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {naacDashboardMaster?.naac_ssr_four_cycle?.map((sub) => (
            <NaacSSR4Card sub={sub} key={sub?._id} />
          ))}
          {naacDashboardMasterLoading ? (
            <Loading />
          ) : (
            naacDashboardMaster?.naac_ssr_four_cycle?.length === 0 && (
              <EmptyMessage title="No data." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <AddNaacSSR4
          onClose={onToggle}
          qcid={qcid}
          onRefetch={naacDashboardMasterRefetch}
        />
      )}
    </>
  );
};

export default NaacSSR4;
