import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import StudentSatisfactorySurveyCard from "./StudentSatisfactorySurveyCard";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import AddStudentSatisfactorySurvey from "./AddStudentSatisfactorySurvey";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
const StudentSatisfactorySurvey = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("student_satisfactory_survey")}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {iqacDashboardMaster?.student_satisfactory?.map((studentSurvey) => (
            <StudentSatisfactorySurveyCard
              studentSurvey={studentSurvey}
              key={studentSurvey?._id}
            />
          ))}
          {iqacDashboardMasterLoading ? (
            <Loading />
          ) : (
            iqacDashboardMaster?.student_satisfactory?.length === 0 && (
              <EmptyMessage title="No student satisfactory survey." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <AddStudentSatisfactorySurvey
          onClose={onToggle}
          qcid={qcid}
          onRefetch={iqacDashboardMasterRefetch}
        />
      )}
    </>
  );
};

export default StudentSatisfactorySurvey;
