import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../../StoreManager/StoreManager.module.css";
import { useTranslation } from "react-i18next";
const IqacMembersCard = ({ member }) => {
  const { t } = useTranslation();
  return (
    <div className={style.smc_card}>
      <div
        className={style.smc_card_contatiner}
        style={{
          justifyContent: "space-between",
          width: "100%",
        }}
      >
        {member?.student?.studentFirstName ? (
          <div className={style.smc_card_contatiner}>
            <img
              src={
                member?.student?.studentProfilePhoto
                  ? `${imageShowUrl}/${member?.student?.studentProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                height: "2.3rem",
                width: "2.3rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.smc_card_contatiner_inner}>
              <h6>{`${member?.student?.studentFirstName ?? ""} ${
                member?.student?.studentMiddleName ?? ""
              } ${member?.student?.studentLastName ?? ""}`}</h6>
              <p>
                {t("designation")}: {member?.designation ?? ""}
              </p>
            </div>
          </div>
        ) : (
          <div className={style.smc_card_contatiner}>
            <img
              src={
                member?.staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${member?.staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                height: "2.3rem",
                width: "2.3rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.smc_card_contatiner_inner}>
              <h6>{`${member?.staff?.staffFirstName ?? ""} ${
                member?.staff?.staffMiddleName ?? ""
              } ${member?.staff?.staffLastName ?? ""}`}</h6>
              <p>
                {t("designation")}: {member?.designation ?? ""}
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default IqacMembersCard;
