import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import IqacMeetingCard from "./IqacMeetingCard";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import AddIqacMetting from "./AddIqacMetting";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import Loading from "../../../Loader/Loading";
const IqacMeeting = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.fm_header_container}
        style={{
          marginTop: "1rem",
        }}
      >
        <h6></h6>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>
      <BorderBottom />
      {iqacDashboardMaster?.meetings?.map((ssrReport) => (
        <IqacMeetingCard ssrReport={ssrReport} key={ssrReport?._id} />
      ))}
      {iqacDashboardMasterLoading ? (
        <Loading />
      ) : (
        iqacDashboardMaster?.meetings?.length === 0 && (
          <EmptyMessage title="No meeting." />
        )
      )}

      {add && (
        <AddIqacMetting
          onClose={onToggle}
          qcid={qcid}
          onRefetch={iqacDashboardMasterRefetch}
        />
      )}
    </>
  );
};

export default IqacMeeting;
